import React, { Component} from 'react';
import Controller from "../main/media/Controller";
import nextId from "react-id-generator";
import {sendAmplitudeData} from "../../utils/Amplitude";

const MEDIA_CARD_MIN_WIDTH = 300;
const MARGIN_MEDIA_CARD = 40;

export default class Doings extends Component {

    constructor(props) {
        super(props);

        this.leftControllerId = nextId();
        this.rightControllerId = nextId();
        this.doingsContainerId = nextId();
        this.state = {
            cards_number: 3,
            current_index: 0
        };
    }

    componentDidMount() {
        let leftController = document.getElementById(this.leftControllerId);
        let rightController = document.getElementById(this.rightControllerId);
        let link = this;

        let doingsContentElement = document.getElementById(this.doingsContainerId);
        this.xDown = null;
        this.yDown = null;

        doingsContentElement.addEventListener('touchstart', function (e) {
            link.handleTouchStart(e)
        });

        doingsContentElement.addEventListener('touchmove', function (e) {
            link.handleTouchMove(e)
        });

        leftController.addEventListener('click', function (event) {
            event.preventDefault();
            link.updateCardIndex(-1);
        });

        rightController.addEventListener('click', function (event) {
            event.preventDefault();
            link.updateCardIndex(1);
        });

        window.addEventListener("resize", function(event) {
            link.resetCardsNumber()
        });

        this.resetCardsNumber();
    }

    updateCardIndex(direction) {
        sendAmplitudeData("Doings: swiped card on " + direction);

        let index = (this.state.current_index + direction) % this.props.array.length;

        if (index < 0) {
            index = this.props.array.length - 1;
        }

        console.log("current_index:" + index);
        this.setState({ current_index: index });
    }

    resetCardsNumber() {
        let mediaContent = document.getElementsByClassName('doings')[0].offsetWidth - document.getElementsByClassName('media_controller')[0].offsetWidth * 2;
        let number = parseInt(mediaContent / (MEDIA_CARD_MIN_WIDTH + MARGIN_MEDIA_CARD), 10);
        if (number > this.props.array.length) {
            number = this.props.array.length;
        }

        number = (number === 0)? 1 : number;

        if (this.state.cards_number !== number) {
            this.setState({ cards_number: number })
        }
    }

    getCards() {
        let link = this;

        let indecesArray = [];

        for (let i = 0; i < link.state.cards_number; i++) {
            let localIndex = (link.state.current_index + i) %  link.props.array.length;

            if (localIndex < 0) {
                localIndex = link.props.array.length - 1;
            }

            indecesArray[indecesArray.length] = localIndex;
        }

        return indecesArray.map(function callback(currentValue, index, array) {
            return link.props.array[currentValue];
        })
    }

    handleTouchStart(evt) {
        const firstTouch = evt.touches[0];
        this.xDown = firstTouch.clientX;
        this.yDown = firstTouch.clientY;
    };

    handleTouchMove(evt) {
        if (!this.xDown || !this.yDown) {
            return;
        }

        let xUp = evt.touches[0].clientX;
        let yUp = evt.touches[0].clientY;

        let xDiff = this.xDown - xUp;
        let yDiff = this.yDown - yUp;

        if ( Math.abs( xDiff ) > Math.abs( yDiff ) ) {
            let sign = xDiff > 0? -1 : 1;
            this.updateCardIndex(sign);
        }

        this.xDown = null;
        this.yDown = null;
    };


    render() {
        return (
            <div className={"doings"}>
                <Controller isLeft={true} id={this.leftControllerId}/>

                <div className={"doings_content"} id={this.doingsContainerId}>
                    {this.getCards()}
                </div>

                <Controller isLeft={false} id={this.rightControllerId}/>
            </div>
        )
    }
}