import React, { Component} from 'react';
import Header from "../main/header/Header";
import strings from "../Localisation";
import axios from 'axios';
import nextId from "react-id-generator";
import Footer from "../main/footer/Footer";
import {sendAmplitudeData} from "../../utils/Amplitude";
import Button from "../main/presentation/Button";
const ADDRESS = 'http://localhost:9191/api/v1/';

export class Controller extends Component {
    constructor(props) {
        super(props);
        this.state = {
            result : {
                content : {
                    key : "..........."
                }
            }
        };

        this.buttonSwipeId = nextId();
    }

    componentDidMount() {
        this.getKey();
        let link = this;

        let swipeButton = document.getElementById(this.buttonSwipeId);
        swipeButton.addEventListener("click", function(event) {
            sendAmplitudeData("Controller: changing key" );
            event.preventDefault();
            link.generateNewKey();
        });
    }

    generateNewKey() {
        axios.get(ADDRESS + 'key.reset').then(
            (response) => {this.getKey() },
            (error) => { this.setState({ result: { content: { key : error } } }) }
        );
    }

    async getKey() {
        axios.get(ADDRESS + 'key.get')
            .then(
                (response) => { this.setState({ result: response.data }) },
                (error) => { this.setState({ result: { content: { key : error } } }) }
            );
    }

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;

        return (
            <div className={"app"}>
                <Header handle={handleStateUpdate.bind(this)} />
                <div className={"container_up_space"} />

                <div className={"controller"}>
                    <div className={"controller__text"}>
                        {strings.controller__your_key + this.state.result.content.key}
                    </div>

                    <Button id={this.buttonSwipeId} text={strings.controller__change_key} />
                </div>

                <div className={"container_down_space"} />
                <Footer />
            </div>
        )
    }
}