import React, { Component } from 'react';
let classNames = require('classnames');

export default class Button extends Component {

    render() {
        let className = classNames("presentation__button", {
            "presentation__button__merch": this.props.type === "merch"
        });

        return (
            <div className={className} id={this.props.id}>
                <div className={"presentation__button_text"}>{this.props.text}</div>
            </div>
        )
    }
}