import React, { Component} from 'react';
import nextId from "react-id-generator";
import axios from "axios";
var jsscompress = require("js-string-compression");
let classNames = require('classnames');

export default class Phone extends Component {
    constructor(prop) {
        super(prop);

        this.phoneImgId = nextId();
    }

    isLocalStorageAvailable(){
        var test = 'test';
        try {
            localStorage.setItem(test, test);
            localStorage.removeItem(test);
            return true;
        } catch(e) {
            return false;
        }
    }

    getType() {
        for (let element of ["small", "middle", "large"]) {
            if (this.props.src.includes(element)) {
                return element;
            }
        }
    }

    downloadFullsize(element) {
        let type = this.getType();
        let link = this;

        if (localStorage.getItem(this.props.src) == null) {
            axios.get(this.props.src.replace(type, "fullsize")).then(function (response) {
                element.src = "data:image/gif;base64," + response.data;

                if (link.isLocalStorageAvailable()) {
                    const hm = new jsscompress.Hauffman();
                    let compressed = hm.compress(element.src);

                    localStorage.setItem(link.props.src, compressed);
                }
            })
        }

        else {
            const hm = new jsscompress.Hauffman();
            let decompressed = hm.decompress(localStorage.getItem(this.props.src));
            element.src = decompressed;
        }
    }

    componentDidMount() {
        let element = document.getElementById(this.phoneImgId);
        element.src = this.props.src;

        // this.downloadFullsize(element)
    }

    render() {
        let className = classNames('functional__phone_container__phone', {
            'functional__phone_container__phone_active': this.props.isActive === true,
            'functional__phone_container__phone_passive': this.props.isActive !== true,
            'functional__phone0': (this.props.maxIndex - this.props.index) === 3,
            'functional__phone1': (this.props.maxIndex - this.props.index) === 2,
            'functional__phone2': (this.props.maxIndex - this.props.index) === 1,
            'functional__phone3': (this.props.maxIndex - this.props.index) === 0
        });

        let classNameGif = classNames('functional__bottom_gif', {
            'functional__phone_container__phone_active': this.props.isActive === true,
            'functional__phone_container__phone_passive': this.props.isActive !== true,
            'functional__bottom_gif0': (this.props.maxIndex - this.props.index) === 3,
            'functional__bottom_gif1': (this.props.maxIndex - this.props.index) === 2,
            'functional__bottom_gif2': (this.props.maxIndex - this.props.index) === 1,
            'functional__bottom_gif3': (this.props.maxIndex - this.props.index) === 0,
        });

        return (
            <div id={this.props.id}>
                <img className={classNameGif} id={this.phoneImgId} />
                <img className={className} src={"/images/main/functional/phone.png"}/>
            </div>
        )
    }
}