import React, { Component} from 'react';
import Text from './Text';
import Title from './Title';
import HowItWorks from './HowItWorks';
import PhoneContainer from './PhoneContainer';

export default class Functional extends Component {

    render() {
        return (
            <div className={"functional"}>
                <div className={"functional_left"}>
                    <Title title={this.props.title} src={this.props.emoji} type={this.props.type} />
                    <Text text={this.props.description} />

                    {
                        (this.props.path)? <HowItWorks path={this.props.path}/> : <div></div>
                    }
                </div>

                <PhoneContainer array={this.props.array} quality={this.props.quality} />
            </div>
        )
    }
}