import '../../App.css';
import React, { Component } from 'react';
import Footer from './footer/Footer'
import Media from './media/Media'
import Control from './control/Control'
import Health from './health/Health'
import Header from './header/Header'
import {Presentation} from "./presentation/Presentation";
import {SpeedMeasure} from "../../utils/SpeedMeasure";

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    handleStateUpdate() {
        this.setState({});
    }

    async runFunction() {
        let speedMeasure = new SpeedMeasure();
        await speedMeasure.getNetworkDownloadSpeed();
        console.log(SpeedMeasure.speed)
        this.setState({})
    }

    componentDidMount() {
        this.runFunction();
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;

        let quality = SpeedMeasure.getResolution([
            Control.getControlArray(),
            Health.getHealthArray()
        ]);

        return (
            <div className="app">
                <Header handle={handleStateUpdate.bind(this)} />
                <Presentation />
                <Control quality={quality} />
                <Health quality={quality} />
                <Media />
                <Footer />
            </div>
        );
    }
}

export default App;
